// Import dependencies
import React, { useState, useEffect } from 'react';
import { NavLink } from 'react-router-dom';

// CSS
import '../../css/TransitionStyles.css';

const Navbar = () => {
    // State
    const [toggleMenu, setToggle] = useState(false);

    // Hooks
    useEffect(() => {
        const bar = document.getElementById('navBar');
        const arrow = document.getElementById('toggleArrow');
        const navItems = document.getElementsByClassName('navBarText');
        const navIcons = document.getElementsByClassName('navBarIcon');

        if (toggleMenu) {
            bar.style.width = '12rem';
            arrow.style.transform = 'translateX(6rem) rotate(180deg)';
            for (let i = 0; i < navItems.length; i++) {
                navItems[i].style.opacity = '100%';
                navItems[i].style.transform = '';
                navIcons[i].style.transform = 'translateX(6rem)';
            };
        } else {
            bar.style.width = '4rem';
            arrow.style.transform = '';
            for (let i = 0; i < navItems.length; i++) {
                navItems[i].style.opacity = '0%';
                navItems[i].style.transform = 'translateX(-15vw)';
                navIcons[i].style.transform = '';
            };
        };
    }, [toggleMenu]);
    
    // Attributes
    const links = [
        {id: 1, title: 'Portfolio', link: '/portfolio', icon: 'work'},
        {id: 2, title: 'Contact Me', link: '/contact', icon: 'mail'}
    ];

    // Methods
    const toggle = () => {
        setToggle(!toggleMenu);
    };

    // Styles
    const navTextStyle = {
        transitionProperty: 'opacity, transform',
        transitionDuration: '300ms',
        transitionTimingFunction: 'ease-in-out'
    };

    return (
        <aside
        id='navBar'
        className='--transitionEase
        flex-shrink-0 text-slate-400 bg-slate-800
        h-screen fixed md:sticky top-0 z-20'>
            <ul>
                <li>
                    <div className='p-2 invert items-center content-center'>
                        <NavLink to='/'>
                            <img
                            src={require('../../assets/CodeStagLogoDarkTransparent.svg').default}
                            alt='Portfolio site logo'
                            className='--inverted w-12 cursor-pointer'/>
                        </NavLink>
                    </div>
                </li>
                <li>
                    <div
                        className='w-full h-12
                        px-2 py-8
                        relative flex items-center content-center
                        cursor-pointer select-none
                        transition-colors duration-200 hover:bg-slate-900 hover:text-[#29e6d0]'
                        onClick={ toggle }>
                            <span
                                id='toggleArrow' 
                                className='inline-block absolute
                                material-icons
                                transition-transform duration-[405ms] left-[30%]'>
                                keyboard_double_arrow_right
                            </span>
                    </div>
                </li>
                {
                    links.map(link => {
                        return (
                            <li key={link.id}>
                                <NavLink 
                                className='
                                w-full h-12
                                relative flex
                                items-center content-center 
                                px-2 py-8 
                                cursor-pointer select-none 
                                transition-color duration-200 hover:bg-slate-900 hover:text-[#29e6d0] hover:-translate-y-1'
                                to={link.link}>
                                    <span
                                    style={navTextStyle}
                                    className='navBarText
                                    text-xl font-display
                                    whitespace-nowrap'>
                                        {link.title}
                                    </span>
                                    <span
                                    className='navBarIcon
                                    transition-transform duration-[405ms]
                                    inline-block 
                                    material-icons absolute left-[30%]'>
                                        { link.icon }
                                    </span>
                                </NavLink>
                            </li>
                        );
                    })
                }
            </ul>
        </aside>
    );
};

export default Navbar;