// Import dependencies
import React from 'react';

// Components
import ProjectCard from './ProjectCard';

// Icons
import IllustratorIcon from '../../assets/icons/adobe-illustrator-icon.svg';
import PhotoshopIcon from '../../assets/icons/adobe-photoshop-icon.svg';
import ReactJSIcon from '../../assets/icons/react-js-icon.svg';
import TailwindIcon from '../../assets/icons/tailwind-css-icon.svg';
import VueIcon from '../../assets/icons/vue-js-icon.svg';
import MongoDBIcon from '../../assets/icons/mongodb-icon.svg';
import ExpressIcon from '../../assets/icons/express-js-icon.svg';
import NodeJSIcon from '../../assets/icons/node-js-icon.svg';
import WebpackIcon from '../../assets/icons/webpack-icon.svg';

// Images
import PerformLivestreamImage from '../../assets/portfolio-perform-livestream-splash.png';
import AuthenticationImage from '../../assets/portfolio-authentication-splash.png';
import BantamWestImage from '../../assets/portfolio-bantam-west-splash.png';
import RMGDashboardImage from '../../assets/portfolio-RMG-dashboard-splash.png';

class ProjectCardManager extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      projectCards: [
        {
          title: 'RMG Dashboard',
          icons: [[ReactJSIcon, 'React'], [TailwindIcon, 'Tailwind CSS'], [WebpackIcon, 'Webpack']],
          date: 2022,
          type: 'Web Developer',
          summary: 'RMG Dashboard is a bespoke web application built to serve live statistics regarding retail installations for brands such as Sonos, Sony and Yamaha.',
          image: RMGDashboardImage,
          alt: 'RMG Dashboard screen',
          link: '/portfolio/rmg-dashboard',
          side: 1
        },
        {
          title: 'Perform Livestream',
          icons: [[VueIcon, 'Vue.js'], [TailwindIcon, 'Tailwind CSS']],
          date: 2022,
          type: 'Web Developer',
          summary: 'Perform Livestream is a streaming service owned and operated by Echelon Productions and is used to deliver learning content to schools around Australia.',
          image: PerformLivestreamImage,
          alt: 'Perform Livestream screen',
          link: '/portfolio/perform-livestream',
          side: 1
        },
        {
          title: 'Log In Authentication',
          icons: [[ReactJSIcon, 'React'], [MongoDBIcon, 'MongoDB'], [ExpressIcon, 'Express'], [NodeJSIcon, 'Node'], [TailwindIcon, 'Tailwind CSS']],
          date: 2022,
          type: 'Designer/Freelancer',
          summary: 'A simple log in system which allows the user to create and log in with an account, attached to a live database.',
          image: AuthenticationImage,
          alt: 'Bantam West Kickstarter screen',
          link: '/portfolio/perform-livestream',
          side: 1
        },
        {
          title: 'Bantam West',
          icons: [[PhotoshopIcon, 'Photoshop'], [IllustratorIcon, 'Illustrator']],
          date: 2021,
          type: 'Designer/Freelancer',
          summary: 'Bantam West is a board game created by Ike Brunicardi. I had the privelage of designing the world map asset used for the game while I was operating my freelancing business, Naturally Chaotic.',
          image: BantamWestImage,
          alt: 'Bantam West Kickstarter screen',
          link: '/portfolio/perform-livestream',
          side: 1
        },
        
      ]
    };
  };

  render () {

    return (
      <div
      className="
      flex flex-col flex-wrap
      w-full relative
      justify-evenly gap-y-16
      items-center
      bg-neutral-50">
        {this.state.projectCards.map((project) => {
          return (
            <ProjectCard data={{...project}} key={'Project Card ' + (Math.random() * 10)}/>
          )
        })}
      </div>
    );
  };
};

export default ProjectCardManager;