// Import dependencies
import React from 'react';

class Title extends React.Component {
    constructor() {
        super();
        this.state = {
            currentOption: '',
            currentIndex: null,
            options: ['Digital Content Coordinator', 'Front-End Developer', 'Freelancer'],
            typeDelay: 75,
            cycleDelay: 1250,
            previousOption: null
        };
    };

    // Lifecycle method
    componentDidMount() {
        this.selectTitle();
    };

    // Methods
    selectTitle = () => {
        let index;
        do {
            index = this.getRandomNumber(0,this.state.options.length - 1);
        } while (index === this.state.previousOption);
        this.setState(
            prevState => {
                return {
                    ...prevState,
                    currentIndex: index,
                    previousOption: index
                };
            },
            function() {
                this.buildWord();
        });
    };

    buildWord = () => {
        if (this.state.currentOption.length !== this.state.options[this.state.currentIndex].length) {
            this.setState(
                prevState => {
                    return {
                        ...prevState,
                        currentOption: this.state.currentOption + this.state.options[this.state.currentIndex].charAt(this.state.currentOption.length)
                    };
                }, function() {
                    setTimeout(this.buildWord, this.state.typeDelay);
                }
            );
        } else {
            setTimeout(this.deleteWord, this.state.cycleDelay);
        };
    };

    deleteWord = () => {
        if (this.state.currentOption.length !== 0) {
            this.setState(
                prevState => {
                    return {
                        ...prevState,
                        currentOption: this.state.currentOption.slice(0, -1)
                    };
                },
                function() {
                    setTimeout(this.deleteWord, this.state.typeDelay);
                }
            );
        } else {
            setTimeout(this.selectTitle, this.state.cycleDelay);
        };
    };

    getRandomNumber = (min, max) => {
        const minInt = Math.ceil(min);
        const maxInt = Math.floor(max);
        return Math.floor(Math.random() * (maxInt - minInt + 1)) + minInt;
    };

    render() {
        return (
            <div>
                <h3 className='text-slate-100 font-body font-semibold text-base sm:text-xl md:text-2xl lg:text-3xl z-10 select-none'>I am a { this.state.currentOption }</h3>
            </div>
        )
    };
};

export default Title;