// Install dependencies
import React from "react";
import dayjs from "dayjs";

// CSS
import "aos/dist/aos.css";

// Plugins
import relativeTime from "dayjs/plugin/relativeTime";
dayjs.extend(relativeTime);

class GlanceCard extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      cardPage: 1,
      isHover: false,
    };
  }

  render() {
    // Methods
    const convertTime = (time) => {
      return dayjs(time).fromNow(true);
    };

    // Store card references
    const cardFirst = this.props.data.cardData[0];

    // Define card elements
    const cardFrontPage = (
      <div
        className="
    flex flex-col flex-none
    text-center w-full h-full
    relative bg-neutral-50"
      >
        <span className="material-icons mt-8 text-4xl text-customGreen select-none">
          {cardFirst.icon}
        </span>
        <h2 className="text-2xl font-display font-semibold text-slate-700">
          {cardFirst.title}
        </h2>
        <div
          className="
      flex flex-col flex-none flex-wrap
      justify-center mx-auto
      py-4 gap-4 w-3/4"
        >
          <p className="font-body text-slate-700">{cardFirst.summary}</p>
          <span className="text-xl font-display font-medium text-customGreen -mb-2">
            {cardFirst.highlight}
          </span>
          <div className="grid grid-cols-2 gap-6 md:gap-8">
            <div>
              <ul className="flex flex-col justify-start text-start">
                {cardFirst.list
                  .slice(0, Math.ceil(cardFirst.list.length / 2))
                  .map((item, index) => {
                    return (
                      <li className='list-disc text-customGreen' key={index}>
                        <span className="font-body font-lg font-semibold text-slate-700 self-start">
                          {item}
                        </span>
                      </li>
                    );
                  })}
              </ul>
            </div>
            <div>
              <ul className="flex flex-col justify-start text-start">
                {cardFirst.list
                  .slice(Math.ceil(cardFirst.list.length / 2))
                  .map((item, index) => {
                    return (
                      <li className='list-disc text-customGreen' key={index}>
                        <span className="font-body font-lg font-semibold text-slate-700 self-start">
                          {item}
                        </span>
                      </li>
                    );
                  })}
              </ul>
            </div>
            <p className="text-xl font-display font-medium text-customGreen pt-4 -mb-2 col-span-2">
              {cardFirst.tools}
            </p>
            <div>
              <ul className="flex flex-col justify-start text-start">
                {cardFirst.toolList
                  .slice(0, Math.ceil(cardFirst.toolList.length / 2))
                  .map((tool, index) => {
                    return (
                      <li className='list-disc text-customGreen' key={index}>
                        <span className="font-body font-lg font-semibold text-slate-700">
                          {tool}
                        </span>
                      </li>
                    );
                  })}
              </ul>
            </div>
            <div>
              <ul className="flex flex-col justify-start text-start">
                {cardFirst.toolList
                  .slice(Math.ceil(cardFirst.toolList.length / 2))
                  .map((tool, index) => {
                    return (
                      <li className='list-disc text-customGreen' key={index}>
                        <span className="font-body font-lg font-semibold text-slate-700">
                          {tool}
                        </span>
                      </li>
                    );
                  })}
              </ul>
            </div>
          </div>
        </div>
        <div className="absolute bottom-0 h-36 w-full flex justify-center">
          <div className="flex flex-col justify-center my-8">
            <h4 className="text-xl text-customGreen">Experience:</h4>
            <span className="text-3xl font-semibold text-customGreen">
              {" "}
              {convertTime(cardFirst.experience)}{" "}
            </span>
          </div>
        </div>
      </div>
    )

    return (
      <div
        data-aos="fade-up"
        data-aos-duration="500"
        data-aos-delay={this.props.data.delay}
        className="flex z-10"
      >
        <div
          className="
        slider w-[20rem] md:w-[24rem] h-[44rem] md:h-[42rem]
        flex flex-none
        relative overflow-hidden
        rounded-lg border border-neutral-200 bg-neutral-50
        hover:shadow-lg hover:-translate-y-2
        transition-transform duration-[150ms] ease-in"
        >
          <div
            className="
          slide absolute
          flex flex-col
          h-full w-full
          justify-center items-center"
          >
            {cardFrontPage}
          </div>
          <div
            className="
           navDots z-10
           flex w-full h-4 py-4
           bg-customGreen
           absolute bottom-0
           gap-4 justify-center
           "
          >
           
          </div>
        </div>
      </div>
    );
  }
}

export default GlanceCard;
