// Import dependencies
import React from 'react';
import ReactDOM from 'react-dom';

// CSS
import styles from '../../css/modules/BlurSphere.module.css';

class BlurSphere extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      size: (Math.floor((Math.random() * 10) + 2)) + 'rem',
      left: (Math.floor((Math.random() * this.props.containerWidth))) + 'px',
      top: (Math.floor((Math.random() * this.props.containerHeight))) + 'px',
      duration: 90 - (Math.floor(Math.random() * (5 - 1 + 1)) + 1),
      xA: (Math.floor(Math.random() * (8 - (-8) + 1)) + (-8)) + 'rem',
      xB: (Math.floor(Math.random() * (8 - (-8) + 1)) + (-8)) + 'rem',
      xC: (Math.floor(Math.random() * (8 - (-8) + 1)) + (-8)) + 'rem',
      xD: (Math.floor(Math.random() * (8 - (-8) + 1)) + (-8)) + 'rem',
      yA: (Math.floor(Math.random() * (8 - (-8) + 1)) + (-8)) + 'rem',
      yB: (Math.floor(Math.random() * (8 - (-8) + 1)) + (-8)) + 'rem',
      yC: (Math.floor(Math.random() * (8 - (-8) + 1)) + (-8)) + 'rem',
      yD: (Math.floor(Math.random() * (8 - (-8) + 1)) + (-8)) + 'rem'
    };
  };

  // Lifecycle hooks
  componentDidMount() {
    // Set anim properties
    const el = ReactDOM.findDOMNode(this);
    
    el.style.setProperty(
      '--xA',
      this.state.xA
    );
    el.style.setProperty(
      '--xB',
      this.state.xB
    );
    el.style.setProperty(
      '--xC',
      this.state.xC
    );
    el.style.setProperty(
      '--xD',
      this.state.xD
    );
    el.style.setProperty(
      '--yA',
      this.state.yA
    );
    el.style.setProperty(
      '--yB',
      this.state.yB
    );
    el.style.setProperty(
      '--yC',
      this.state.yC
    );
    el.style.setProperty(
      '--yD',
      this.state.yD
    );
    el.style.setProperty(
      '--duration',
      this.state.duration + 's'
    );
    
  };

  render () {
    return (
      <div
      style={{ 
        left: this.state.left, 
        top: this.state.top,
        width: this.state.size,
        height: this.state.size
      }}
      className={styles.BlurSphere}></div>
    )
  };
};

export default BlurSphere;