// Import dependencies
import React, { useState } from 'react';

const TooltipIcon = (props) => {

  const [toggle, setToggle] = useState(false);

  const isHovering = () => {
    setToggle(true);
  };

  const notHovering = () => {
    setToggle(false);
  };

  const showText = {
    opacity: 1
  };
  const hideText = {
    opacity: 0
  };

  return (
    <div className='flex flex-col items-center'>
      <img onMouseOver={isHovering} onMouseOut={notHovering} src={ props.imageURL } alt={ props.altText } className='w-6 sm:w-8 md:w-10 lg:w-12 h-6 sm:h-8 md:h-10 lg:h-12'/>
      <div style={ toggle ? showText : hideText } className='bg-slate-800 absolute mt-10 sm:mt-12 md:mt-14 lg:mt-16 p-2 rounded-md transition-all duration-150 ease-in'>
        <span className='text-slate-100 font-display text-xs md:text-sm lg:text-base select-none'>{ props.tooltip }</span>
        <div className='absolute left-1/2 -translate-x-1/2 -top-2/3 border-[1rem] w-2 h-2 border-b-slate-800 border-t-transparent border-x-transparent'/>
      </div>
    </div>
  );

};

export default TooltipIcon;