// Import dependencies
import React from 'react';
import { NavLink } from 'react-router-dom';

// Components
import TooltipIcon from '../functions/TooltipIcon';

class ProjectCard extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  };

  render () {

    // Prop reference
    const data = this.props.data;

    return (
      <div data-aos='fade-up' data-aos-duration='500' className="w-full flex flex-col">
        <div
        className="
        w-1/2 h-full mx-auto overflow-x-hidden
        rounded-lg border border-neutral-200 bg-neutral-50
        flex flex-col">
          <img 
            src={data.image}
            alt={data.alt}
            className="w-full"/>
          <div className="w-full h-auto flex">
            <div className="w-2/3 flex flex-col mt-1 sm:mt-2 md:mt-4 lg:mt-8 ml-2 sm:ml-4 md:ml-8 lg:ml-14">
              <h2
              className="
              text-slate-700
              font-display font-bold
              text-xl sm:text-2xl md:text-3xl lg:text-4xl">
                {data.title}
              </h2>
              <h4
              className="
              text-customGreen
              font-display font-semibold
              text-base sm:text-xl md:text-2xl lg:text-3xl">
                {data.date}
              </h4>
              <p
              className="
              text-slate-700
              pt-2 sm:pt-4 md:pt-6 lg:pt-8
              font-body font-medium
              text-sm sm:text-base md:text-xl lg:text-2xl">
                {data.summary}
              </p>
              <NavLink
              to={data.link}
              className="
              flex rounded-full text-customGreen border-customGreen border-2 bg-transparent text-center justify-center
              w-20 sm:w-24 md:w-28 lg:w-32 h-8 sm:h-10 md:h-12 lg:h-14 my-2 sm:my-4 md:my-6 lg:my-8
              hover:bg-customGreen hover:text-neutral-50 transition-all duration-150">
                <span
                className="font-display font-medium text-sm sm:text-base md:text-lg lg:text-xl self-center">
                  See more
                </span>
            </NavLink>
            </div>
            <ul 
            className="w-1/3 h-full mt-1 sm:mt-2 md:mt-4 lg:mt-8 mr-2 sm:mr-4 md:mr-8 lg:mr-14 flex flex-row sm:gap-1 md:gap-2 justify-end transition-all duration-150">
              {data.icons.map((icon) => {
                return (
                  <li key={'Software Icon ' + (Math.random() * 10)}>
                    <TooltipIcon 
                          tooltip={ icon[1] }
                          imageURL={ icon[0] }
                          altText='Software icon'
                      />
                  </li>
                )
              })}
            </ul>
          </div>
        </div>
      </div>
    );
  };
};

export default ProjectCard;