// Import dependencies
import React from "react";
import { NavLink } from "react-router-dom";
import { Helmet } from "react-helmet-async";

// Images
import PerformLivestreamImage from "../assets/portfolio-perform-livestream-splash.png";
import AuthenticationImage from "../assets/portfolio-authentication-splash.png";
import BantamWestImage from "../assets/portfolio-bantam-west-splash.png";
import RMGDashboardImage from "../assets/portfolio-RMG-dashboard-splash.png";

const HomePage = () => {
  const pages = [
    {
      image: RMGDashboardImage,
      name: "RMG Dashboard",
      link: "/portfolio/RMG-dashboard",
      alt: "RMG Dashboard Portfolio Image",
    },
    {
      image: PerformLivestreamImage,
      name: "Perform Livestream",
      link: "/portfolio/perform-livestream",
      alt: "Perform Livestream Portfolio Image",
    },
    {
      image: AuthenticationImage,
      name: "Authentication App (MERN)",
      link: "/portfolio/authentication-app",
      alt: "Authentication App Portfolio Image",
    },
    {
      image: BantamWestImage,
      name: "Bantam West",
      link: "/portfolio/bantam-west",
      alt: "Bantam West Portfolio Image",
    },
  ];

  return (
    <>
      <Helmet>
        <title>Portfolio</title>
        <meta name="description" content="See examples of my recent work." />
        <meta name="robots" content="index, follow" />
      </Helmet>
      <div className="flex flex-col w-3/5 mx-auto mt-24 ">
        <h1
          className="text-slate-100 font-display font-bold text-2xl sm:text-3xl md:text-4xl
            self-start mb-4"
        >
          My Portfolio
        </h1>
        <p
          className="font-body text-slate-100
            text-xs sm:text-sm md:text-lg lg:text-xl self-start mb-2 sm:mb-4 md:mb-6 lg:mb-8"
        >
          Below are some examples of my recent work, both as a front-end
          developer and as a designer. <br />
          Please feel free to click on their links to see more about the
          projects!
        </p>
        <ul className="flex flex-wrap gap-8 w-full">
          {pages.map((page, index) => {
            return (
              <li key={index}>
                <NavLink to={page.link}>
                  <div className="max-w-3xl relative flex flex-col">
                    <h1
                      className="text-slate-100 font-display font-bold text-xl sm:text-2xl md:text-3xl
                      self-start mb-4"
                    >
                      {page.name}
                    </h1>
                    <div className="group">
                      <img
                        src={page.image}
                        alt={page.alt}
                        className="w-full rounded-md group-hover:brightness-50 transition-all duration-200"
                      />
                      <span className="font-display font-medium text-white text-sm sm:text-xl md:text-2xl lg:text-3xl self-center text-center absolute top-1/2 -translate-y-1/2 left-1/2 -translate-x-1/2 hidden opacity-0 group-hover:block group-hover:opacity-100 transition-all duration-200">
                        Click to see more
                      </span>
                    </div>
                  </div>
                </NavLink>
              </li>
            );
          })}
        </ul>
      </div>
    </>
  );
};

export default HomePage;
