// Import dependencies
import React from "react";
import { Helmet } from "react-helmet-async";

// Assets
import { ReactComponent as LinkedInIcon } from "../assets/icons/linkedin-icon.svg";

const ContactMePage = () => {
  return (
    <>
      <Helmet>
        <title>Contact Me</title>
        <meta
          name="description"
          content="Want to get in touch? Please check out the options on this page if you are interested in having a chat about my work."
        />
        <meta name="robots" content="index, follow" />
      </Helmet>
      <div className="h-[93.5vh] w-full pt-8 md:pt-16 pb-4 md:pb-8 bg-neutral-50 ml-10 md:ml-0">
        <div
          className="
      flex flex-col relative
      justify-center items-center text-center
      h-auto top-1/4 pb-12"
        >
          <h2
            className="
          mt-4
          text-slate-700
          font-display font-bold
          text-xl sm:text-2xl md:text-3xl lg:text-4xl"
          >
            Want to get in touch?
          </h2>
          <p
            className="
          text-slate-700
          pt-8 w-1/2
          font-body font-medium
          text-sm sm:text-base md:text-xl lg:text-2xl"
          >
            You can contact me in a couple of ways! <br /> Feel free to reach
            out using my personal phone number, email address or via social
            media, the relevant contact details for which are below!
          </p>
          <div className="flex flex-col gap-y-8 md:grid md:gap-12 md:grid-cols-3 mt-8">
            <div className="flex flex-col justify-center text-center">
              <span className="text-4xl md:text-5xl material-icons select-none text-[#1B998B]">
                call
              </span>
              <div className="group md:mt-2">
                <span
                  className="
              text-slate-700
              group-hover:text-[#1B998B]
              font-display font-bold
              select-none
              text-2xl md:text-3xl
              transition-colors duration-[150ms]"
                >
                  Phone
                </span>
                <p
                  className="
              text-slate-700
              group-hover:text-[#1B998B]
              font-display font-medium
              md:text-xl
              transition-colors duration-[150ms]"
                >
                  0491 007 587
                </p>
              </div>
            </div>
            <div className="flex flex-col">
              <span className="text-4xl md:text-5xl material-icons select-none text-[#1B998B]">
                mail
              </span>
              <div className="group md:mt-2">
                <a
                  href="mailto:liam_nixon@outlook.com"
                  className="
              text-slate-700
              group-hover:text-[#1B998B]
              font-display font-bold
              text-2xl md:text-3xl
              transition-colors duration-[150ms]"
                >
                  Email
                  <p
                    className="
              text-slate-700
              group-hover:text-[#1B998B]
              font-display font-medium
              text-base md:text-xl
              transition-colors duration-[150ms]"
                  >
                    liam_nixon@outlook.com
                  </p>
                </a>
              </div>
            </div>
            <div className="flex flex-col justify-center text-center">
              <LinkedInIcon className=" w-10 md:w-12 h-6 sm:h-8 md:h-10 lg:h-12 fill-[#1B998B] mx-auto" />
              <div className="group md:mt-2">
                <a
                  href="https://www.linkedin.com/in/liam-nixon-63759611a"
                  target="_blank"
                  rel="noreferrer"
                  className="
              text-slate-700
              group-hover:text-[#1B998B]
              font-display font-bold
              text-2xl md:text-3xl
              transition-colors duration-[150ms]"
                >
                  LinkedIn
                  <p
                    className="
              text-slate-700
              group-hover:text-[#1B998B]
              font-display font-medium
              text-base md:text-xl
              transition-colors duration-[150ms]"
                  >
                    Click Me!
                  </p>
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default ContactMePage;
