// Import dependencies
import React from 'react';

// Components
import GlanceCard from './GlanceCard';

class GlanceCardManager extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      glanceCards: [
        {
          cardData: [
            { 
              icon: 'code',
              title: 'Front-End Developer',
              summary: 'I dedicate myself to delivering fast, responsive web solutions, built with industry standard Javascript frameworks.',
              highlight: 'Programming Languages',
              list: ['HTML', 'CSS', 'JavaScript', 'TypeScript'],
              tools: 'Dev Tools:',
              toolList: ['Express', 'Git', 'Node', 'React', 'Tailwind', 'Vite', 'Vue'],
              experience: '11-19-2021'
            }
          ],
          length: [1],
          delay: 0
        },
        
        {
          cardData: [
            { 
              icon: 'design_services',
              title: 'Designer',
              summary: 'I have firm experience with professional design software and have produced bespoke content across a variety of platforms.',
              highlight: 'Skills',
              list: ['Digital Assets', 'Print Assets', 'Social Media' ],
              tools: 'Software:',
              toolList: ['Adobe Suite', 'Autodesk Suite', 'Creator Studio', 'Microsoft Office'],
              experience: '07-18-2020'
            }
          ],
          length: [1],
          delay: 150
        },
        {
          cardData: [
            { 
              icon: 'work',
              title: 'Freelancer',
              summary: 'Freelancing has equipped me with the business acumen to understand client needs at the macro level.',
              highlight: 'Skills',
              list: ['Business strategy', 'Client relations', 'Competitor analysis', 'CRM', 'Financial management', 'IT support', 'Project management', 'SEO'],
              tools: 'Management Software:',
              toolList: ['Creator Studio', 'Google Analytics', 'Salesforce', 'Survey Monkey'],
              experience: '08-03-2020'
            }
          ],
          length: [1],
          delay: 300
        },
        {
          cardData: [
            { 
              icon: 'school',
              title: 'University Student',
              summary: 'My studies have a strong focus on modern web development and interactive design practices.',
              highlight: 'Monash University',
              list: ['Began study in Q3 2020.', 'Expected to finish Q2 2024.'],
              tools: `Area's of Study`,
              toolList: ['Major in Interactive Media', 'Minor in IT for Business', 'Minor in Web Development'],
              experience: '07-18-2020'
            }
          ],
          length: [1],
          delay: 450
        },
      ]
    };
  };
  
  render () {
    return (
      <div
      className="
      flex flex-row flex-wrap
      max-w-screen-lg h-auto relative
      mx-auto px-4
      justify-evenly gap-8
      bg-neutral-50">
        {this.state.glanceCards.map((gCard) => {
          return (
            <GlanceCard key={'Glance Card' + (Math.random() * 10)} data={{...gCard}} />
          )
        })}
      </div>
    )
  };
};

export default GlanceCardManager;