// Import dependencies
import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';
import { Helmet } from 'react-helmet-async';

// Components
import TooltipIcon from '../../components/functions/TooltipIcon';

// Images
import ReactJSIcon from '../../assets/icons/react-js-icon.svg';
import TailwindIcon from '../../assets/icons/tailwind-css-icon.svg';
import WebpackIcon from '../../assets/icons/webpack-icon.svg';

const RMGDashboard = () => {

    // Initialize data state
    const initState = () => {
        return {
            role: 'Lead Front-End Developer',
            date: '2022-2023',
            description: 'RMG Dashboard is a bespoke web application built to serve live statistics regarding retail installations for brands such as Sonos, Sony and Yamaha.'
        };
    };

    // State
    const [data] = useState(initState());
    const [toggle, setToggle] = useState(false);

    // Methods
    const isHovering = () => {
        setToggle(true);
      };
    
      const notHovering = () => {
        setToggle(false);
      };

    // Dynamic styling
    const showBar = {
        backgroundColor: 'rgba(30, 41, 59, 0.75)',
        bottom: 0
      };
      const hideBar = {
        backgroundColor: 'rgba(30, 41, 59, 0)',
        bottom: '-12.5%'
      };

      const showText = {
        color: 'rgba(241, 245, 249, 1)'
      };

      const hideText = {
        color: 'rgba(241, 245, 249, 0)'
      };

    return (
        <>
        <Helmet>
        <title>Perform Livestream Portfolio</title>
        <meta
          name="description"
          content="A case study on the RMG Dashboard web application that I was contracted to work on by Retail Maintenance Group."
        />
        <meta name="robots" content="index, follow" />
      </Helmet>
        <div className='flex flex-col items-center w-3/5 mx-auto mt-24'>
            <nav className='flex flex-row w-auto gap-0.5 sm:gap-1 lg:gap-2 self-start'>
                <NavLink to='/portfolio' className='font-body font-medium text-slate-100 text-xs sm:text-sm md:text-base lg:text-lg hover:underline'>Portfolio</NavLink>
                <span className='material-icons text-slate-100 text-xs sm:text-sm md:text-lg lg:text-xl select-none'>keyboard_arrow_right</span>
                <NavLink to='/portfolio/perform-livestream' className='font-body font-medium text-slate-100 text-xs sm:text-sm md:text-base lg:text-lg hover:underline'>RMG Dashboard</NavLink>
            </nav>
            <h1 className='text-slate-100 font-display font-bold text-2xl sm:text-3xl md:text-4xl
            self-start mb-4'>RMG Dashboard</h1>
            <div onMouseOver={isHovering} onMouseOut={notHovering} className='w-full overflow-hidden relative'>
                <img src={require('../../assets/portfolio-RMG-dashboard-splash.png')} alt="Perform Livestream Splash" className='w-full'></img>
                <div style={ toggle ? showBar : hideBar } className='absolute w-full lg:h-1/5 xl:h-[12.5%] transition-all duration-150'>
                    <p style={ toggle ? showText : hideText } className='w-full h-full p-2 font-body font-medium text-xs md:text-sm lg:text-base transition-all duration-150'>{ data.description }</p>
                </div>
            </div>
            <hr className='w-full mt-2 sm:mt-4 md:mt-6 lg:mt-8' />
            <div className='flex flex-row justify-between w-full'>
                <div className='flex flex-row justify-start w-auto gap-4 sm:gap-8 md:gap-12 lg:gap-16'>
                    <div className='w-auto self-center'>
                        <h3
                        className='font-body font-medium text-slate-100
                        text-sm sm:text-base md:text-xl lg:text-2xl'>
                            Role:
                        </h3>
                        <h4
                        className='font-body text-slate-100
                        text-xs sm:text-sm md:text-lg lg:text-xl'>
                            { data.role }
                        </h4>
                    </div>
                    <div className='w-auto self-center'>
                        <h3
                        className='font-body font-medium text-slate-100
                        text-sm sm:text-base md:text-xl lg:text-2xl'>
                            Date:
                        </h3>
                        <h4
                        className='font-body text-slate-100
                        text-xs sm:text-sm md:text-lg lg:text-xl'>
                            { data.date }
                        </h4>
                    </div>
                </div>
                <div className='flex flex-row w-auto gap-x-2 justify-end my-4'>
                    <h3
                    className='font-body font-medium self-center text-slate-100
                    text-sm sm:text-base md:text-xl lg:text-2xl'>
                        Tools Used:
                    </h3>
                    <TooltipIcon 
                        tooltip='React'
                        imageURL={ ReactJSIcon }
                        altText='React Icon'
                    />
                    <TooltipIcon 
                        tooltip='Tailwind CSS'
                        imageURL={ TailwindIcon }
                        altText='Tailwind Icon'
                    />
                    <TooltipIcon 
                        tooltip='Webpack'
                        imageURL={ WebpackIcon }
                        altText='Webpack Icon'
                    />
                </div>
            </div>
            <hr className='w-full mb-2 sm:mb-4 md:mb-6 lg:mb-8' />
            <div className='flex gap-x-4'>
                <h2 className='text-slate-100 font-display font-bold text-4xl sm:text-5xl md:text-6xl'>"</h2>
                <p className='font-body text-slate-100 text-xl sm:text-2xl md:text-3xl inline-block self-center uppercase'><em>Welcome to your dashboard</em></p>
                <h2 className='text-slate-100 font-display font-bold text-4xl sm:text-5xl md:text-6xl'>"</h2>
            </div>
            <h1 className='text-slate-100 font-display font-bold text-2xl sm:text-3xl md:text-4xl self-start mb-4'>Project Aim</h1>
            <p
            className='font-body text-slate-100
            text-xs sm:text-sm md:text-lg lg:text-xl self-start mb-2 sm:mb-4 md:mb-6 lg:mb-8'>
                The primary focus of this project was to create a bespoke, data visualisation web-application for RMG (Retail Maintenance Group). 
                The app needed to be compatible with a python-driven Django back-end and to handle large volume data from a live PostGRE database, 
                to reflect real-world data relating to RMG's many clients, including brands such as Sony, Sonos and Yamaha. This data reflected product 
                installations found at retailers such as JB Hi-Fi throughout Australia and New Zealand.
            </p>
            <h1 className='text-slate-100 font-display font-bold text-2xl sm:text-3xl md:text-4xl self-start mb-4'>Challenges</h1>
            <p
            className='font-body text-slate-100
            text-xs sm:text-sm md:text-lg lg:text-xl self-start mb-2 sm:mb-4 md:mb-6 lg:mb-8'>
                As the sole front-end developer on this project, it was my responsibility to not only create robust, flexible and scalable components for 
                the web application, but to liase with project stakeholders in order to determine scope and a project timetable.
                <br/> <br/>
                Due to the scale of this project, there were many technical considerations, particularly regarding securtity, accuracy and performance of 
                the app. These challenges needed to be carefully and attentively managed, in order to provide a high-quality platform for RMG clients and other 
                stakeholders to interact with.
            </p>
            <h1 className='text-slate-100 font-display font-bold text-2xl sm:text-3xl md:text-4xl self-start mb-4'>The Process</h1>
            <p
            className='font-body text-slate-100
            text-xs sm:text-sm md:text-lg lg:text-xl self-start mb-2 sm:mb-4 md:mb-6 lg:mb-8'>
                This app originally was created using standard HTML, providing a working - though simple realisation of the concept desired by RMG, 
                my initial responsibilities were to migrate the site over to a React framework, in order to provide more flexibility and performance than 
                would otherwise be achieved through basic HTML and JavaScript.
                <br/> <br/>
                Once this was achieved, I was provided with a design brief by the client and tasked with updating the UI/UX of the application. To achieve this, 
                I made use of the CSS framework TailwindCSS, in order to provide quick, yet bespoke styling to the platform, in line with the specifications provided.
                <br/><br/>
                In addition to creating a more user-friendly interface, I was also responsible for adding further functionality to the website. When building a project 
                of this scope, I adhere to the ATOMIC principles and aim to create standardized, scalable components which can be populated with live data, regardless of 
                the data volume/complexity. Some tools I made use of for this, particularly in regards to keeping the app performant, included making use of pagination, 
                memoized components and ensuring that each component was responsible for a single purpose (ATOMIC principles).
            </p>
            <h1 className='text-slate-100 font-display font-bold text-2xl sm:text-3xl md:text-4xl self-start mb-4'>Outcome</h1>
            <p
            className='font-body text-slate-100
            text-xs sm:text-sm md:text-lg lg:text-xl self-start mb-2 sm:mb-4 md:mb-6 lg:mb-8'>
                This project is still ongoing, but at present I have concluded my contract with RMG, as the front-end is at a place where they are satisfied and wanting to 
                spend some time working on their back-end and general strategy moving forward. It is possible that I may return to add small changes, or add analytics to the 
                platform via Google Analytics 4, however.
            </p>
        </div></>
    );
};

export default RMGDashboard;